.containerHome {
  display: flex;
  justify-content: center;
}

.containerHome .imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.containerHome .imageContainer .perfil {
  max-width: 400px;
  height: auto;
  margin: 0px;
  border-radius: 50%;
}

.containerQuemSOu {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.titleQuemSOu {
  font-weight: 600;
  font-size: 40px;
  margin-bottom: 0;
  margin-right: 10px;
}

.containerQuemSOu div {
  align-items: flex-end;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: linear-gradient(20deg, #3e523e,#284d30, #235e1f, #023a02 );
}

.containerHome .sobreContainer p {
  margin-bottom: 0;
  font-size: 20px;
  text-align: justify;
}

.barraFinal {
  display: flex;
  justify-content:center;
  align-items:center;
  color:white;
  background-color:#f3f1f1 ;
  background: linear-gradient(20deg, #3e523e,#284d30, #235e1f, #023a02 );
  bottom: 0px;
  width: 100%;
  height: 50px;
  position: fixed;
}
