.container {
  background-color: #e5e2e2;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.containerHome {
  display: flex;
  justify-content: center;
}

.containerHome .imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.containerHome .imageContainer img {
  max-width: 400px;
  height: auto;
  margin: 0px;
}

.containerHome .sobreContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 35%;
  margin-left: 20px;
}

.containerHome .sobreContainer .ola {
  font-size:50px;
  font-weight: 600;
  margin-top: 0;
}

.containerHome .sobreContainer .nome {
  font-size: 60px;
  font-weight: 600;
  margin-top: 0;
}

.containerHome .sobreContainer span {
  font-size:50px;
  margin-top: 0;
}

.barraFinal {
  display: flex;
  justify-content:center;
  align-items:center;
  color:white;
  background-color:#f3f1f1 ;
  background: linear-gradient(20deg, #3e523e,#284d30, #235e1f, #023a02 );
  bottom: 0px;
  width: 100%;
  height: 50px;
  position: fixed;
}
