header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  background-color: white;
  height: 100px;
  position: absolute;
  width: 100%;
  top: 0;
}

.nameContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nameContainer .titleHeader {
  margin-bottom: 0;
  font-size: 35px;
  font-weight: 900;
  color: #22611e;
}

.nameContainer span {
  margin-top: 0;
  font-size:12px;
  color: #22611e;
}

.menuContainer {
  display: flex;
  align-items: center;
}

.menuContainer .menu {
  text-decoration: none;
  text-transform: uppercase;
  margin-left: 10px;
  margin-right: 10px;
  color: #22611e;
}

.menuContainer .menu:hover {
  color: rgb(75, 74, 74);
  text-decoration:underline;
}
